body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* your customization styles */
.your-element {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; /* to take all available space */
  height: 100%; /* to take all available space */
  border-radius: 10px;
  padding: 0.5rem;
}

.your-element h3 {
  margin-top: 0;
}

.your-element p:last-child {
  margin-bottom: 0;
}

.your-element-green {
  background: #2ecaac;
}

.your-element-red {
  background: #ff6252;
}

.your-element-blue {
  background: #54c6f9;
}

.your-row-head {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; /* to take all available space */
  height: 100%; /* to take all available space */
  padding: 10px;
  font-weight: bold;
  background: beige;
}

.your-col-head {
  padding: 15px;
  font-weight: bold;
  background: beige;
}



